<template> 
  <div class="app-container">
    <div class="table-container">
    <el-table ref="orderTable"
              :data="list"
              border>
      <el-table-column label="编号" width="80" align="center">
        <template slot-scope="scope">{{scope.row.id}}</template>
      </el-table-column>
      <el-table-column label="菜品名称" width="200" align="center">
        <template slot-scope="scope">{{scope.row.name}}</template>
      </el-table-column>
      <el-table-column label="菜品单价" width="200" align="center">
        <template slot-scope="scope">￥{{scope.row.price}}</template>
      </el-table-column>
      <el-table-column label="菜品数量" width="200" align="center">
        <template slot-scope="scope">{{scope.row.quantity}}</template>
      </el-table-column>
    </el-table>
    </div>
  </div>
</template> 
<script>
  import {getAllOrderDetailList} from "../../../api/order";

  const defaultListQuery = {
    name: null,
    pageNum: 1,
    pageSize: 5,
    categoryId: null,
  };
  export default {
    name: "productList",
    data() {
      return {
        list: []
      }
    },
    created() {
      this.getList()
    },
    methods: {
      getList() {
        getAllOrderDetailList(this.$route.query.id).then(response => {
          console.log(this.$route.query.id);
          this.list = response.data;
        });
      }
    }
  }
</script>






