import request from '@/utils/request'


export function getAllOrderList(pageNum,pageSize) {
  return request({
    url:'/manager/queryAllOrder',
    method:'get',
    params:{
      pageNum,
      pageSize
    }
  })
}
export function searchOrderListByTime(pageNum,pageSize,startTime,endTime) {
  return request({
    url:'/manager/queryByTime',
    method:'get',
    params:{
      pageNum,
      pageSize,
      startTime,
      endTime
    }
  })
}

export function getAllOrderDetailList(orderId) {
  return request({
    url:'/manager/orderDetail',
    method:'get',
    params:{
      orderId
    }
  })
}



